                h4 NIST CSF (National Institute of Standards and Technology - Cybersecurity Framework) - Marco de Ciberseguridad del Instituto Nacional de Estndares y Tecnologa.
                p Este marco establece una gua en la que cualquier empresa pueda recibir, comprender, administrar y reducir los riesgos cibernticos junto con sus redes y datos. Este marco permite la comprensin del lenguaje comn y enfoca buenas prcticas en ciberseguridad.
                  br
                  br
                  |El Instituto Nacional de Normas y Tecnologa (NIST), una agencia perteneciente al Departamento de Comercio de los Estados Unidos, desarroll este marco. El cual utiliza un lenguaje cmodo que gua a las compaas en general a gestionar algn impacto negativo de seguridad digital y as fortalecer la informacin.
