    p.mb-5 <b>HITRUST CSF</b> fue desarrollado por <i>Health Information Trust Alliance</i> (HITRUST) y es el marco de seguridad ms adoptado en la industria de la salud de los Estados Unidos. <b>HITRUST</b> originalmente desarroll su CSF (<i>Cybersecurity Framework</i>  Marco de ciberseguridad) para enfocarse en elementos clave y riesgos inherentes a la industria de la salud, como las consideraciones de <b>HIPAA</b> (Ley de Responsabilidad y Portabilidad de Seguros de Salud) pero desde entonces han actualizado el marco con controles ms amplios que se aplicaran a cualquier organizacin.
