        p.mb-5 Se refieren a la posibilidad que se presente algn tipo de evento se pueda, en cualquier instante de tiempo, producir un dao material o inmaterial sobre los activos informticos y los sistemas de informacin. Las amenazas son consideradas como los ataques cometidos por personas internas o externas, que pueden ocasionar daos a la infraestructura tecnolgica, a los sistemas de informacin o a la misma informacin que circula en la organizacin.
          br
          br
          |Si bien cada vez son ms y ms especializadas, a continuacin se mencionan algunas de las amenazas ms comunes y que mayor afectacin provocan en nuestros recursos y medios digitales utilizados frecuentemente.
      .col-lg-6(data-aos="fade-left"): img(src='@/assets/curso/20.svg')
    AcordionA.mb-5(tipo="a" clase-tarjeta="tarjeta bg__2-claro")
      .row(titulo="Spam").px-5
        .col-md-3
          figure
            img(src='@/assets/curso/21.svg', alt='Texto que describa la imagen')
        .col-md-9.mb-4.mb-md-0
          p Correo no deseado. Mucha gente se ve afectada cada ao por correo no deseado que falsific su informacin engaando al usuario para que siga algunos de los enlaces que normalmente contiene y que los redirecciona a sitios previamente programados o en ocasiones hace que se ejecuten algunos scripts o fragmento de cdigo que realizarn alguna actividad prohibida sobre nuestros recursos tecnolgicos.
      .row(titulo="Pharming ").px-5
        .col-md-3
