        .col-md-9.mb-4.mb-md-0
          p Tiene por principal objetivo convencer al usuario de que visite un sitio web malicioso e ilegtimo redireccionando la URL legtima u original a una URL ilegtima, una vez dentro, el objetivo de los cibercriminales es conseguir que el usuario les d su informacin personal. 
      .row(titulo="Phishing ").px-5
        .col-md-3
          figure
            img(src='@/assets/curso/23.svg', alt='Texto que describa la imagen')
        .col-md-9.mb-4.mb-md-0
          p Es una de las amenazas informticas ms fciles de ejecutar. Consiste en enviar correos electrnicos falsos o mensajes que se parecen a los correos electrnicos enviados por compaas legtimas. As, se hace pensar al usuario que es la compaa legtima y aumentan las probabilidades de que se comparta informacin personal y financiera, algunos ejemplos podemos encontrarlos en mensajes de banco o entidades financieras donde lo que se busca es robar las credenciales y contraseas de acceso a sus cuentas. 
      .row(titulo="Ransomware ").px-5
        .col-md-3
          figure
