        .col-md-9.mb-4.mb-md-0
          p Este tipo de amenaza busca secuestrar o restringir el acceso a las computadoras e informacin de sus vctimas para luego solicitar un pago a cambio de recuperar el control de sus datos.
      .row(titulo="Spyware ").px-5
        .col-md-3
          figure
            img(src='@/assets/curso/25.svg', alt='Texto que describa la imagen')
        .col-md-9.mb-4.mb-md-0
          p Es un programa de computador malicioso que parece un software legtimo. Mientras est instalado en el ordenador del usuario, se ejecuta automticamente y espa su sistema capturando datos que permitan identificar el comportamiento del uso de los medios tecnolgicos por parte del usuario o en algunas ocasiones elimina sus archivos.
      .row(titulo="Malware ").px-5
        .col-md-3
          figure
            img(src='@/assets/curso/26.svg', alt='Texto que describa la imagen')
        .col-md-9.mb-4.mb-md-0
          p Es un software malicioso, este es el nombre comn dado a varias amenazas de seguridad que se infiltran y daan un recurso informtico.
      .row(titulo="Virus ").px-5
        .col-md-3
          figure
            img(src='@/assets/curso/27.svg', alt='Texto que describa la imagen')
        .col-md-9.mb-4.mb-md-0
          p Un virus informtico es un tipo de programa o cdigo malicioso escrito para modificar el funcionamiento de un equipo, siempre est oculto en un software o sitio web legtimo e infecta el ordenador afectado y puede extenderse todos los que se encuentran en su lista de contactos.
