      h2 1.4 Vulnerabilidades 
    p.mb-5 Una vulnerabilidad informtica es una debilidad en el software o en el hardware que permite a un posible atacante comprometer cualquiera de los elementos de la triada de la seguridad como es la integridad, disponibilidad o confidencialidad del sistema o de los datos que procesa.
