        p.mb-0 Por errores en la configuracin o parametrizacin de las herramientas que normalmente utilizamos por ejemplo al utilizar contraseas no seguras, al no actualizar el sistema operativo o antivirus de nuestros dispositivos.
          br
          br
          |Para clasificarlos a continuacin, se mencionan algunas de las vulnerabilidades ms comunes: 
