    #t_1_5.titulo-segundo(data-aos="fade-right")
      h2 1.5 Activos de informacin
    p.mb-5 Este trmino se relaciona con todos esos elementos tecnolgicos o relacionados con la tecnologa que la organizacin utiliza para el cumplimiento de sus metas o core del negocio. Segn la norma ISO/IEC 27001 se entiende como activo todo aquello que es importante y que la organizacin valora por lo tanto debe de protegerse.
      br
      br
      |Desde el punto de vista personal, activos informticos sern todos aquellos elementos o herramientas tecnolgicas tanto <i>hardware</i> (computadores, celulares, tabletas, PDA, entre otros) o elementos <i>software</i> como aplicaciones, correo electrnico, redes sociales, entre otras que son utilizadas dentro del contexto laboral o uso personal.
      br
      br
      |De acuerdo a la norma ISO/IEC 27001, es necesario clasificar e inventariar todos los activos de informacin de una empresa luego de ser identificados, esto se hace como cumplimiento al Modelo de Seguridad y Privacidad de la Informacin, el cual presenta la siguiente forma:
    .row.mb-5
      .col-md-6.col-xl.mb-4.mb-xl-0
        //- .tarjeta-numerada debe ir acompaado de una de una de estas clases => 
        //- .color-primario, .color-secundario, .color-acento-contenido, .color-acento-botones
